<template>
    <Table :columns="columns" :data="data"></Table>
</template>
<script>
    import axios from "axios";
    export default {
         data () {
            return {
                columns: [
                    {
                        title: '',
                        key: 'order_sn'
                    },
                    {
                        title: '',
                        key: 'business_name'
                    },
                    {
                        title: '',
                        key: 'duration'
                    },
                    {
                        title: '',
                        key: 'machine_sn'
                    },
                    {
                        title: '',
                        key: 'order_status'
                    },
                    {
                        title: '',
                        key: 'order_type'
                    },
                    {
                        title: '',
                        key: 'pay_type'
                    },
                    {
                        title: '',
                        key: 'payable_money'
                    },
                    {
                        title: '',
                        key: 'action',
                        width: 170,
                        align: 'center',
                        render: (h, params) => {
                            return h('div',[
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            this.show(params.row);
                                        }
                                    }
                                }, ''),
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            let isOk = confirm(`${params.row.payable_money}`);
                                            if(isOk) {
                                                axios.get("http://"+location.hostname+"/home/customer/payOrderByBalance",{
                                                    order_id: params.row.id
                                                }).then(res => {
                                                    if(res.data.code==1) {
                                                        alert("");
                                                    } else {
                                                        alert(res.data.msg);
                                                    }
                                                });
                                            }
                                        }
                                    }
                                }, ''),
                                h('Button', {
                                    props: {
                                        type: 'primary',
                                        size: 'small'
                                    },
                                    style: {
                                        // marginRight: '5px'
                                    },
                                    on: {
                                        click: () => {
                                            let isOk = confirm("");
                                            if(isOk) {
                                                axios.get("http://"+location.hostname+"/home/customer/delOrder",{
                                                    order_id: params.row.id
                                                }).then(res => {
                                                    if(res.data.code==1) {
                                                        alert("");
                                                    } else {
                                                        alert(res.data.msg);
                                                    }
                                                });
                                            }
                                        }
                                    }
                                }, '')
                            ]);
                        }
                    }
                ],
                data: [
                ]
            }
        },
        methods: {
             show (data) {
                this.$Modal.info({
                    title: '',
                    content: `
                        : ${data.order_sn}<br />
                        ${data.after_money}<br />
                        ${data.before_money}<br />
                        ${data.business_sn}<br />
                        : ${data.business_name}<br />
                        : ${data.duration}<br />
                        : ${data.machine_sn}<br />
                        : ${data.order_note}<br />
                        : ${data.order_status}<br />
                        : ${data.order_type}<br />
                        : ${data.pay_price}<br />
                        : ${data.pay_time}<br />
                        : ${data.pay_type}<br />
                        : ${data.payable_money}<br />
                        : ${data.price}<br />
                        : ${data.resource}<br />
                        : ${data.resource_type}<br />
                        : ${data.serial_number}<br />
                        `
                })
            }
        },
        mounted() {
            axios.get("http://"+location.hostname+"/home/customer/orderList").then(res => {
                if(res.data.code==1) {
                    this.data = res.data.data;
                } else {
                    console.error(res.data.msg);
                }
            });
        }
    }
</script>